@import url("https://fonts.googleapis.com/css2?family=Inter&family=Lexend:wght@300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/*********************************/
/*             General           */
/*===============================*/
html {
  @apply scroll-smooth;
}

body {
  @apply text-[15px] text-black;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply font-poppins;
}

p {
  @apply leading-relaxed;
}

/********************/
/*     Buttons      */
/*==================*/
/* Note: If you want to add your own colors, you can do so by going to this tailwind.config.js file  */
.btn {
  @apply h-10 px-6 inline-flex items-center justify-center font-medium tracking-wide border align-middle transition duration-500 ease-in-out text-base text-center;
  /*  Button icons */
  /* Button Link */
}
.btn.btn-lg {
  @apply h-12 px-6 text-lg;
}
.btn.btn-sm {
  @apply h-8 px-4 text-sm;
}
.btn.btn-icon {
  @apply p-0 h-10 w-10 inline-flex items-center text-center justify-center text-base;
}
.btn.btn-icon.btn-lg {
  @apply h-12 w-12;
}
.btn.btn-icon.btn-sm {
  @apply h-8 w-8;
}
.btn.btn-link {
  @apply relative p-0 border-none after:content-[""] after:absolute after:h-px after:w-0 after:right-0 after:bottom-0 after:left-0 after:transition-all after:duration-500;
}
.btn.btn-link:hover {
  @apply after:w-full after:right-auto;
}

/*********************************/
/*             Menu              */
/*===============================*/
.navbar {
  @apply fixed top-0 left-0 right-0 z-999 bg-transparent py-3 transition-all duration-500 font-poppins;
}
.navbar .navigation {
  @apply w-full m-0;
}
.navbar .navigation .navbar-nav {
  @apply mt-4 flex flex-row overflow-y-scroll h-24;
}
.navbar .navigation .navbar-nav .nav-link {
  @apply block p-3 text-[15px] text-black hover:text-amber-500 dark:text-white/60 dark:hover:text-white font-medium;
}
.navbar .navigation .navbar-nav .active .nav-link {
  @apply text-amber-500 dark:text-white;
}
.navbar .navigation.ms-auto .navbar-nav li:last-child .nav-link {
  @apply pe-0;
}
.navbar .navigation.me-auto .navbar-nav li:first-child .nav-link {
  @apply ps-0;
}
.navbar .navbar-brand {
  @apply p-0;
}
.navbar .navbar-brand .l-dark,
.navbar .navbar-brand .logo-dark-mode {
  @apply hidden;
}
.navbar .navbar-brand .l-light,
.navbar .navbar-brand .logo-light-mode {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .navbar-brand .l-dark {
    @apply inline-block !important;
  }
  .navbar .navbar-brand .l-light {
    @apply hidden !important;
  }
}
.navbar .menu-social .login-btn-primary {
  @apply hidden;
}
.navbar .menu-social .login-btn-light {
  @apply inline-block;
}
@media (max-width: 991.98px) {
  .navbar .menu-social .login-btn-primary {
    @apply inline-block !important;
  }
  .navbar .menu-social .login-btn-light {
    @apply hidden !important;
  }
}
.navbar.is-sticky {
  @apply bg-white dark:bg-black shadow dark:shadow-gray-800;
}
.navbar.is-sticky .navbar-brand .l-dark {
  @apply inline-block;
}
.navbar.is-sticky .navbar-brand .l-light {
  @apply hidden;
}
.navbar.is-sticky .menu-social .login-btn-primary {
  @apply inline-block;
}
.navbar.is-sticky .menu-social .login-btn-light {
  @apply hidden;
}

@media (min-width: 992px) {
  .navbar {
    @apply py-3;
  }
  .navbar .navigation {
    @apply flex w-auto;
  }
  .navbar .navigation .navbar-nav {
    @apply h-auto mt-0 overflow-visible;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply border-0 hover:bg-transparent;
  }
  .navbar .navigation .navbar-nav.nav-light .nav-link {
    @apply block p-3 text-white/60 hover:text-white;
  }
  .navbar .navigation .navbar-nav.nav-light .active .nav-link {
    @apply text-white;
  }
  .navbar .nav-icons .collapse-btn {
    @apply hidden;
  }
  .navbar.is-sticky {
    @apply py-3;
  }
  .navbar.is-sticky .navbar-nav .nav-link {
    @apply text-black;
  }
  .navbar.is-sticky .navbar-nav .active .nav-link {
    @apply text-amber-500;
  }
  .navbar.is-sticky .navbar-nav.nav-light .nav-link {
    @apply text-black hover:text-amber-500 dark:text-white/60 dark:hover:text-white;
  }
  .navbar.is-sticky .navbar-nav.nav-light .active .nav-link {
    @apply text-amber-500 dark:text-white;
  }
}
@media (max-width: 991px) {
  .navbar {
    @apply bg-white shadow dark:shadow-gray-800 dark:bg-black !important;
  }
  .navbar .navigation .navbar-nav {
    @apply block h-full max-h-80;
  }
  .navbar .navigation .navbar-nav .nav-link {
    @apply text-black;
  }
  .navbar .navigation .navbar-nav .active .nav-link {
    @apply text-amber-500;
  }
  .navbar .navigation .navbar-nav .menu-social {
    @apply mb-5 !important;
  }
}
/* // _footer.scss */
/*********************************/
/*         Footer                */
/*===============================*/
.footer .foot-subscribe input::placeholder {
  @apply text-gray-200;
}

/*********************************/
/*             Helper            */
/*===============================*/
.container,
.contacinr-fluid {
  @apply relative;
}

/*********************************/
/*         Tobii Lightbox        */
/*===============================*/
/* Tobii Lighbox */
.tobii > button.tobii__close svg,
.tobii > button.tobii__prev svg,
.tobii > button.tobii__next svg {
  @apply h-9 w-auto;
}

.tobii__counter {
  @apply text-base;
}

.tobii-zoom {
  @apply block;
}

.tobii-zoom__icon {
  @apply hidden;
}

#grid {
  @apply p-0;
}

/*****************/
/*    Shapes     */
/*****************/
/* Shapes */
.shape {
  @apply absolute right-0 -bottom-px left-0;
}
.shape > svg {
  @apply w-full h-auto;
  transform: scale(2);
  transform-origin: theme("transformOrigin.top") theme("transformOrigin.center");
}

/*****************/
/*  back-to-top  */
/*****************/
.back-to-top {
  @apply hidden;
}

/*****************/
/*    Switcher   */
/*****************/
.label .ball {
  transition: transform 0.2s linear;
  @apply translate-x-0;
}

.checkbox:checked + .label .ball {
  @apply translate-x-6;
}

/*****************/
/*   Preloader   */
/*****************/
#preloader {
  background-image: linear-gradient(45deg, #ffffff, #ffffff);
  z-index: 99999;
  @apply fixed inset-0;
}
#preloader #status {
  @apply absolute left-0 right-0 top-1/2 -translate-y-1/2;
}
#preloader #status .spinner {
  @apply w-10 h-10 relative my-[100px] mx-auto;
}
#preloader #status .spinner .double-bounce1, #preloader #status .spinner .double-bounce2 {
  @apply w-full h-full rounded-full bg-amber-500/60 absolute top-0 left-0;
  animation: sk-bounce 2s infinite ease-in-out;
}
#preloader #status .spinner .double-bounce2 {
  animation-delay: -1s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
/*********************************/
/*             Testi            */
/*===============================*/
.customer-testi {
  @apply cursor-e-resize;
}
.customer-testi .content {
  @apply before:content-[""] before:absolute before:left-1/2 before:-bottom-[4px] before:box-border before:border-8 before:rotate-[45deg] before:border-t-transparent before:border-e-white dark:before:border-e-slate-900 before:border-b-white dark:before:border-b-slate-900 before:border-s-transparent before:shadow-testi dark:before:shadow-gray-700;
}
.customer-testi .content:before {
  transform-origin: 0 0;
}

/* RTL */
html[dir=rtl] .tns-inner, html[dir=rtl] .tns-nav, html[dir=rtl] .slider, html[dir=rtl] .slider2, html[dir=rtl] .swiper-slider-hero {
  direction: ltr !important;
}
html[dir=rtl] .tns-visually-hidden {
  left: 0 !important;
  visibility: hidden;
}

/*****************/
/*  video iframe */
/*****************/
@media (max-width: 640px) {
  [data-type] iframe, [data-type] video {
    width: 500px;
    height: 300px;
  }
}
@media (max-width: 425px) {
  [data-type] iframe, [data-type] video {
    width: 360px;
    height: 260px;
  }
}
@media (max-width: 375px) {
  [data-type] iframe, [data-type] video {
    width: 100%;
    height: auto;
  }
}
/* personal Hero */
.personal-wrapper {
  @apply before:content-[""] before:absolute before:bottom-40 before:start-[45rem] before:w-[90rem] before:h-[35rem] ltr:before:rotate-[115deg] rtl:before:rotate-[65deg] before:bg-amber-500;
  @apply after:content-[""] after:absolute after:bottom-96 after:start-[30rem] after:w-[75rem] after:h-40 ltr:after:rotate-[115deg] rtl:after:rotate-[65deg] after:bg-amber-500/10 after:-z-1;
}

@media (min-width: 1024px) and (max-width: 1140px) {
  .personal-wrapper {
    @apply before:start-40 before:w-[75rem] before:h-[30rem] after:start-0 after:bottom-80 after:w-[75rem] after:h-60;
  }
}
@media (max-width: 768px) {
  .personal-wrapper {
    @apply before:start-40 after:start-0;
  }
}
@media (max-width: 767px) {
  .personal-wrapper {
    @apply before:start-0 after:-start-40;
  }
}
/*********************************/
/*             Contact           */
/*===============================*/
/* Validation */
.error {
  @apply my-2 mx-0 hidden text-red-600;
}

#ajaxsuccess {
  @apply text-base w-full hidden clear-both my-2 mx-0;
}

.error_message,
#success_page {
  @apply p-2.5 mb-5 text-center rounded-md;
}

.error_message {
  @apply bg-red-600/5 text-red-600;
}

.contact-loader {
  @apply hidden;
}

#success_page {
  @apply bg-emerald-600/5 text-emerald-600;
}